import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder,FormControl,FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ItemService } from '../item.service';
import { ToastController } from '@ionic/angular';
import { Camera, CameraOptions } from "@ionic-native/camera/ngx";
import { File } from '@ionic-native/file/ngx';
import * as firebase from 'firebase';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.page.html',
  styleUrls: ['./add-product.page.scss'],
})
export class AddProductPage implements OnInit {

  new_item_form: FormGroup;
  randomId = Math.random().toString(36).substr(2, 5);
  addPicId = 'thumb_'+this.randomId;
  downloadURL = [];
  constructor(
    private router: Router,
    public formBuilder: FormBuilder,
    public itemService:ItemService,
    private toastController:ToastController,
    private camera: Camera,
    private file: File
  ) { }

  ngOnInit() {
    this.new_item_form = this.formBuilder.group({
      title: new FormControl('',Validators.required),
      price: new FormControl('',Validators.required),
      category: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required)
    })
  }

  createItem(value) {
    var self = this;
    var storageRef = firebase.storage().ref();
    storageRef.child(this.addPicId).getDownloadURL().then(function(url) {
      var image = url;
      if(image == null) {
        image = "/assets/default.png";
      }
      self.itemService.createItem(value.title,value.price,value.category,image,value.description);
    });
    
    this.new_item_form.reset();
    this.goBack();
    this.presentToast();
  }

  async presentToast() {
    const toast = await this.toastController.create({
      color:'success',
      message: 'Item Added Successfully',
      position:'top',
      duration: 2000
    });
    toast.present();
  }
  
  goBack(){
    this.router.navigate(['/tabs/tab1']);
  }

  async pickImage() {
    var self = this;
    try {
     const options: CameraOptions = {
       quality:50,
       destinationType: this.camera.DestinationType.DATA_URL,
       encodingType: this.camera.EncodingType.JPEG,
       mediaType: this.camera.MediaType.PICTURE
     }

     const result = await this.camera.getPicture(options);

     const image = `data:image/jpeg;base64,${result}`;
     const pictures = firebase.storage().ref(this.randomId);
     pictures.putString(image, 'data_url');

     setTimeout(() => { this.showImage(); }, 2000);

   } catch(e) {
     console.error(e);
   }
   }

   showImage() {
    var storageRef = firebase.storage().ref();
    storageRef.child(this.randomId).getDownloadURL().then(function(url) {
      var test = url;
      document.querySelector('img').src = test;
    });
   }

}
